<script>
    import { selectionStore } from "./selectionStore.js";
    const { selectionTitle, updateTitle, save } = selectionStore;
    import SelectionFooter from "./SelectionFooter.svelte";

    export let isRegion = false;
    export let selectionLength = false;

    let isEditing = false;
    let titleInput;
    let currentTitle;

    function startEditing() {
        isEditing = true;
        currentTitle = $selectionTitle(isRegion);
        // todo display changes of the title on the Item component as well + in the selection record list as well
        setTimeout(() => titleInput && titleInput.focus(), 0);
    }

    function handleKeydown(event) {
        if (event.key === 'Enter') {
            finishEditing();
        }
    }

    function finishEditing() {
        isEditing = false;
        updateTitle(currentTitle, isRegion);
        save(isRegion);
    }

    let userQuery = "";
    let userResults = [];
    let showDropdown = false;

    let users = {};

    async function searchUsers() {
        if (userQuery.trim().length < 1) {
            userResults = [];
            return;
        }

        const url = `${window.location.origin}/search/user?q=${encodeURIComponent(userQuery)}`;
        const response = await fetch(url);
        const data = await response.json();

        userResults = data.users;
        showDropdown = true;
        console.log(showDropdown);
        console.log(userResults);
        console.log(userResults.length);
    }

    function selectUser(user) {
        users[user.id] = { id: user.id, username: user.username };
        userQuery = "";
        userResults = [];
        showDropdown = false;
    }

    function removeUser(id) {
        delete users[id];
        users = { ...users };
    }
</script>

<div id="selection-modal" class="modal fade" tabindex="-1" aria-labelledby="selection-modal-label" aria-hidden="true">
    <div class="modal-background"/>
    <div class="modal-content">
        <div class="modal-card-head media mb-0 is-middle">
            <div class="title is-4 mb-0 media-content">
                <i class="fa-solid fa-book-bookmark"></i>
                {#if isEditing}
                    <input
                        bind:this={titleInput}
                        bind:value={currentTitle}
                        on:keydown={handleKeydown}
                        on:blur={finishEditing}
                        class="input selection-title"
                    />
                {:else}
                    <span on:click={startEditing} on:keyup={null}>
                        {$selectionTitle(isRegion)}
                        <span class="pl-3 smaller has-text-link">
                            <i class="fas fa-edit"/>
                        </span>
                    </span>
                {/if}
                ({selectionLength})
            </div>
            <button class="delete media-left" aria-label="close"/>
        </div>

        <section class="modal-card-body">
            <slot/>

            <h4 class="title is-6 mt-3">Shared with</h4>

            {#if Object.keys(users).length === 0}
                <p class="is-size-7 has-text-grey">No users added yet.</p>
            {:else}
                <ul>
                    {#each Object.entries(users) as [id, user]}
                        <li class="is-flex is-justify-content-space-between py-1">
                            <span>{users.username}</span>
                            <button class="delete is-small" on:click={() => removeUser(id)} />
                        </li>
                    {/each}
                </ul>
            {/if}

            <div class="field autocomplete">
                <div class="control">
                    <input
                        class="input"
                        placeholder="Search users..."
                        bind:value={userQuery}
                        on:input={searchUsers}
                        on:focus={() => (showDropdown = true)}
                    />
                </div>

                {#if showDropdown && userResults.length > 0}
                    <ul class="dropdown-results box">
                        {#each userResults as user}
                            <li class="dropdown-item" on:click={() => selectUser(user)} on:keyup={null}>
                                {users.username}
                            </li>
                        {/each}
                    </ul>
                {/if}
            </div>
        </section>

        <SelectionFooter {isRegion}/>
    </div>
</div>

<style>
    .input:focus {
        box-shadow: none;
    }
    .selection-title {
        margin-top: -0.2rem;
        padding-bottom: 0.75rem;
        width: 75%;
        font-size: 1.4rem;
        border: none;
        border-bottom: 1px solid var(--bulma-link);
        border-radius: 0;
    }
    .autocomplete { position: relative; }
    .dropdown-results {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        z-index: 20;
        border: 1px solid #ddd;
        max-height: 200px;
        overflow-y: auto;
        padding: 0;
        margin-top: 4px;
    }
    .dropdown-item {
        padding: 8px 10px;
        cursor: pointer;
    }
    .dropdown-item:hover {
        background: #f5f5f5;
    }
</style>
